import logo from './logo.svg';
import './App.css';
import {Marker, Popup, TileLayer, Tooltip} from "react-leaflet";

import { MapContainer } from 'react-leaflet/MapContainer'
import L from 'leaflet'
import {useEffect, useState} from "react";
import { useLeafletContext } from '@react-leaflet/core'
import MapLayer from "./components/MapLayer";
import SearchBar from "./components/SearchBar";

const url = process.env.NODE_ENV !== 'production' ? 'http://localhost:3002/buses/list' : '/buses/list'

function Square(props) {
    const context = useLeafletContext()

    useEffect(() => {
        const bounds = L.latLng(props.center).toBounds(props.size)
        const square = new L.Rectangle(bounds)
        const container = context.layerContainer || context.map
        container.addLayer(square)

        return () => {
            container.removeLayer(square)
        }
    })

    return null
}

function App() {
    const [buses, setBuses] = useState([])
    const [center, setCenter] = useState([-34.9, -56.2])

    const [busQuery, setBusQuery] = useState("")

    const onSearchBarChange = (event) => {
        setBusQuery(event.target.value)
    }

    useEffect(() => {
        const query_url = url + '?' + new URLSearchParams({
            'busList': busQuery,
        })
        fetch(query_url).then(res => res.json()).then(body => setBuses(body))
        const intervalId = setInterval(() => {  //assign interval to a variable to clear it.

            fetch(query_url).then(res => res.json()).then(body => setBuses(body))
        }, 10000)

        return () => clearInterval(intervalId); //This is important
    }, [busQuery])

    return (
        <div className="App">
            <header className="App-header">
                <SearchBar value={busQuery} onSearchBarChange={onSearchBarChange}/>
                <MapContainer center={center} zoom={11} scrollWheelZoom={true}  >
                    <MapLayer buses={buses} busQuery={busQuery}/>
                </MapContainer>
            </header>
        </div>
    );
}

export default App;
