import L from 'leaflet';
import bus from './bus.png'
import {Icon} from "leaflet/src/layer";
import React from "react";
import {renderToString} from "react-dom/server";

const BusLabel = ({line}) => {
    return <div className="rounded rounded-xl font-bold p-1 text-gray-200 bg-blue-700 text-center">
        <p className="" >{line}</p>
    </div>

}

const BusIcon = (line) => new L.DivIcon({
    className: 'bus-icon',
    iconSize: [40, 12],
    bgPos: [0,0],
    html: renderToString(<BusLabel line={line}/>)
})

export { BusIcon };