import {CircleMarker, Marker, Popup, TileLayer, Tooltip, useMap, useMapEvents} from "react-leaflet";
import {BusIcon} from "../leaflet_extras/busMarker";
import {useEffect, useState} from "react";


const MapLayer = ({buses, busQuery}) => {
    const [center, setCenter] = useState()
    const [zoom, setZoom] = useState()
    const [bounds, setBounds] = useState()

    const map = useMap()
    const mapEvents = useMapEvents({
        move: (e) => {
            setZoom(map.getZoom())
            setCenter(map.getCenter())
            setBounds(map.getBounds())
        },
    })

    const getBuses = () => {
        return buses.filter(bus => busQuery === "" || bus.line.includes(busQuery))
    }

    return (
        <>
            <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                url="https://tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {
                bounds && center && buses && getBuses().map((bus) => {
                        if (
                            bus.location.coordinates[0] > bounds._southWest.lng && bus.location.coordinates[0] < bounds._northEast.lng &&
                            bus.location.coordinates[1] > bounds._southWest.lat && bus.location.coordinates[1] < bounds._northEast.lat
                        ) {
                            return (
                                <Marker key={"" + bus.busId + bus.lineVariantId} icon={BusIcon(bus.line)}
                                        position={[bus.location.coordinates[1], bus.location.coordinates[0]]}>
                                    <Popup>
                                        <ul>
                                            <li>Compaa: {bus.company}</li>
                                            <li>Linea: {bus.line}</li>
                                            <li>Origen: {bus.origin}</li>
                                            <li>Destino: {bus.destination}</li>
                                        </ul>
                                    </Popup>
                                </Marker>
                            )
                        }
                        else
                        {
                            return false
                        }

                    }
                )
            }
        </>
    )

}

export default MapLayer